
import React from 'react'
import NeastedContent from './NeastedContent';


class Fetch extends React.Component {
   
    // Constructor 
    constructor(props) {
        super(props);
   
        this.state = {
            items: [],
            DataisLoaded: false
        };
    }
   
    // ComponentDidMount is used to
    // execute the code 
    componentDidMount() {
        fetch("https://yousef3ahmed.github.io/virtualize-data-/db.json")
            .then((res) => res.json())
            .then((json) => {
                this.setState({
                    items: json,
                    DataisLoaded: true
                });
            })
    }

    render() {
        const { DataisLoaded, items } = this.state;
        if (!DataisLoaded) return <div class="spinner-grow" role="status">
        <span class="sr-only">Loading...</span>
      </div>
        // <div> <h1> Pleses wait some time.... </h1> </div> ;
   
        return (
        <>
            {
                items.map( ( item , index) => {
                    
                    if( item.id == this.props.id ){
                        return(
                            item["lectures"].map(( vals , indx ) =>{
                                return(
                                    <NeastedContent  id = { indx } title = { vals.title }  Arrinside = { vals.lessons } ></NeastedContent>
                                )
                                
                            })
                        )
                    }
                      
                }) 
            }

        </>
    );
}
}


export default Fetch;